<!--  -->
<template>
  <div>
          <ifbp-template ref="mortgageRef"
                        tplId="mortgageTemplate"
                        :funnode="funnode"
                        :nexuskey="nexusKey"
                        :tplData="mortgageData"
                        :tplResetFun="mortgageResetFun"
                        :tplMethods="mortgageTplMethods"
                        @form-confirm-click="mortgageFormConfirm"
                        @form-cancel-click="mortgageFormCancel"
                        @edit-table-click="mortgageEditTableRow"
                        @delete-table-click="mortgageDeleteTableRow"
                        show-type="table-form"
                       >
          </ifbp-template>
    <!--   -->
    <el-dialog
      title=""
      v-model="mortgageDelVisible"
      :modal="true"
      size="tiny">
      <span> </span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="mortgageDelVisible = false, this.delId=''"> </el-button>
        <el-button type="primary" @click="mortgageDeleteClick"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import {ylsBusi} from '../../../../../common/js/publicData.js';
export default {
  props: ['pk_customer','source_bill','invisible'],
  data() {
    let oThis = this;
    let validator = function(rule, value, callback) {};
    return {
      mortgageDelVisible: false,
      rmoveindex: '',
      delId: '',
      funnode:'BT008',
      nexusKey:'mortgage_busi_apply',
      mortgageData: {
       
      },
      mortgageResetFun: function($node) {
        if(oThis.invisible) {
          return;
        }

        let $table = $node.find("el-table");
        let operateArr = [
          {
            title: '',

            icon: 'edit'
          },
          {
            title: '',

            icon: 'delete'
          }
        ];
        let operateHtml = this.getTableOperateHtml(operateArr);
        $table.append(operateHtml);
        return $node[0].outerHTML;
      },
      mortgageTplMethods: {
        // form
      }
    };
  },
  //
  created() {
  },
  //
  mounted() {
    this.request();
  },
  methods: {
//
    request() {
        if (this.source_bill != '') {
          this.requestMortgage();
      }
    },
    closeAddForm(){
        this.$refs['mortgageRef'].comp.formShow = false;
    },
    //
    requestMortgage( ) {
      let data = {
        pageNum: 0,
        pageSize: 10,
        searchParams: {
          searchMap: {
            custCondList:[
              {key:'source_bill',oper:'=',value:this.source_bill},
              {key:'guarantee_type',oper:'=',value:'COLLATERAL'},
              ]
          }
        }
      };
      this.$http({
        url: ylsBusi + 'prj/mp/page',
        headers: { 'Content-Type': 'application/json' },
        method: 'post',
        data: data,
        dataType: 'json'
      })
        .then(res => {
          this.originalValue = res.data.data.content;
          this.$refs['mortgageRef'].setData(
            'Mortgage_t',
            JSON.parse(JSON.stringify(this.originalValue))
          );
        })
        .catch(() => {
          this.$message({
            message: '',
            type: 'error'
          });
        });
    },
    //
    mortgageFormConfirm() {
      //
      let url;
      let data = this.$refs.mortgageRef.comp.Mortgage;
      if(data.pk_mortgage_pledge){
         url = ylsBusi+'prj/mp/update';
      }else{
         url = ylsBusi+'prj/mp/create';
      }
      data.source_bill = this.source_bill;
      data.guarantee_type='COLLATERAL'; //
          this.$http({
            url, //
            headers: { 'Content-Type': 'application/json' },
            method: 'post',
            data: JSON.parse(JSON.stringify(data))
          })
            .then(res => {
              if (res.data.success === true) {
                this.$message({
                  message: '',
                  type: 'success'
                });
                this.originalValue = res.data.data;
                //
                let linarraydata = this.$refs.mortgageRef.getData(
                  'Mortgage_t'
                );
                /**@augments  
                 * @augments 
                 * @augments 
                 */
                
                if (this.rmoveindex !== '') {
                      linarraydata.splice(this.rmoveindex, 1, this.originalValue);
                } else {
                  //
                       linarraydata.unshift(this.originalValue);
                }
                //
                // linarraydata.push(this.originalValue);
                //
                this.$refs.mortgageRef.setData(
                  'Mortgage_t',
                  JSON.parse(JSON.stringify(linarraydata))
                );
                //
                this.$refs['mortgageRef'].comp.formShow = false;
                this.$emit("closeAddForm");
              } else {
                this.$message({
                  message: res.data.error.errorMessage,
                  type: 'error'
                });
              }
            })
            .catch(() => {
              this.$message({
                message: '',
                type: 'error'
              });
            });
    },
    // form
    mortgageFormCancel: function(type) {
      if (type === 'form') {
        this.$refs['mortgageRef'].comp.formShow = false;
        this.$emit("closeAddForm");
      } else {
        this.$refs['mortgageRef'].getTableComp().closeExpandRow();
      }
    },
    //
    mortgageEditTableRow: function(scope) {
      //
      this.rmoveindex = scope.$index;
      //
      let row = scope.row;
      this.$refs['mortgageRef'].getTableComp().expandRow(row);
      this.$refs['mortgageRef'].formShow = false;
      //mortgageRef
      this.$refs['mortgageRef'].setData('Mortgage', row);
    },
    //
    mortgageDeleteTableRow: function(scope) {
      this.mortgageDelVisible = true;
      this.delId = scope.row.pk_mortgage_pledge;
    },
    //
    mortgageDeleteClick() {
      this.$http({
        url: ylsBusi + 'prj/mp/deleteById',
        headers: { 'Content-Type': 'application/json' },
        method: 'post',
        dataType: 'json',
        data: this.delId
      })
        .then(res => {
          if (res.data.success === true) {
            this.$message({
              message: '',
              type: 'success'
            });
            this.requestMortgage();
          } else {
            this.$message({
              message: res.data.error.errorMessage,
              type: 'error'
            });
          }
        })
        .catch(e => {
          this.$message({
            message: '',
            type: 'error'
          });
        });
      this.mortgageDelVisible = false;
      this.delId = '';
    }
  }
};
</script>
<style>

</style>



