<template>
  <!---->
  <div>
    <ifbp-template ref='contProviderRef'
      tplId='contProvider'
      :funnode='funnode'
      :nexuskey='nexusKey'
      :tplData='contProviderData'
      :tplResetFun='contProviderResetFun'
      @form-confirm-click='contProviderFormConfirm'
      @form-cancel-click='contProviderFormCancel'
      show-type='table-form'
      :methods='t_Methods'
      @edit-table-click='contProviderFormedit'
      @delete-table-click='contProviderFormdelete'
    >
    </ifbp-template>

    <!--   -->
    <el-dialog
      title=''
      v-model='contProviderDelVisible'
      :modal='true'
      size='tiny'>
      <span></span>
      <span slot='footer' class='dialog-footer'>
        <el-button @click="contProviderDelVisible = false , this.delId=''"> </el-button>
        <el-button type='primary' @click='contProviderDeleteClick'> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>

import {ylsBusi} from '../../../../../common/js/publicData.js'

export default {
  //vue
  props: ['source_bill','type'],
  data() {
    let oThis = this;
    //

    return {
      scrollDom: document.getElementsByClassName('view')[0],
      contProviderDelVisible: false,
      rmoveindex: '',
      delId: '',
      //
      contProviderIcons: [
        {
          icon: 'plus',
          click: function() {
            if (oThis.source_bill === '') {
              oThis.$message({
                message: '',
                type: 'error'
              });
              return;
            }
            let uitemplateComp = oThis.$refs.contProviderRef.comp;
            let table = uitemplateComp.$refs['contProvider_t_ref'];
            table.closeExpandRow();
            uitemplateComp.formShow = true;
            //
            oThis.$refs.contProviderRef.setData('contProvider', {
              // mobile:'13'
            });
            oThis.rmoveindex ='';
            uitemplateComp.$refs['contProvider_ref'].resetFields();
          }
        }
      ],
      //type=prj ui_id:8e13c344-fe4b-4e0c-a171-8e18395ee236 a7715073-8a24-420a-86de-9110786e8e4b
      funnode: oThis.type==='prj' ? 'BT008':'BT008',
      nexusKey: oThis.type==='prj' ? 'prjProviderUI' : (oThis.type==='prjApproval' ? 'prjApproval_provider' : 'prjProviderUI'),
      contProviderData: {
        linkManParams:{},
        providerParams:{},
        rentParams:{'pk_customer':'yls_dev100000001pqc'} //   pk
      },
      //
      contProviderResetFun: function($node) {
        //(type === prj)
        // let $salesCustomer = this.getNodeById($node, '5zouflppncd');  //
        // if($salesCustomer.length) {
        //   $salesCustomer.attr('v-on:trigger', 'saleRefChange'); 
        // }

        // let $pkLinkMan = this.getNodeById($node, '6bt70kv30vb');  //
        // if($pkLinkMan.length) {
        //   $pkLinkMan.attr('v-on:trigger', 'linkManRefChange'); 
        // }
        //end

        let $pkProvider= this.getNodeById($node, 'ibgjz2m7kkd');  //
        if($pkProvider.length) {
          $pkProvider.attr('v-on:trigger', 'pkProviderRefChange'); 
        }

        //idowwkujphew
        let id_co='';
        if(oThis.type==='prj')
          id_co='i71amo6wy18';
        else if(oThis.type==='contr')
          id_co='';
        else if(oThis.type==='prjApproval')
          id_co='npnshehd4lh';
        let $table = this.getNodeById($node, id_co);
        let operateArr = [
          {
            title: '',

            icon: 'edit'
          },
          {
            title: '',

            icon: 'delete'
          }
        ];
        let operateHtml = this.getTableOperateHtml(operateArr);
        $table.append(operateHtml);
        return $node[0].outerHTML;
      },

      t_Methods : {
        // saleRefChange: function(type, data) {
        //   if(type === 'change') {
        //     let param = {'pk_customer':data.value[0].id};
        //     oThis.$refs.contProviderRef.setData('providerParams',param);
        //   }
        // },
        // linkManRefChange: function(type,data){
        //   if(type === 'change') {
        //     let param = {'pk_customer':data.value[0].id};
        //     oThis.$refs.contProviderRef.setData('linkManParams',param);
        //   }
        // },
        pkProviderRefChange: function(type,data){
          if(type === 'change') {
            debugger;
            oThis.$refs.contProviderRef.comp.contProvider.provider_code=data.value[0].refcode; //
            oThis.$refs.contProviderRef.comp.contProvider.identity_num=data.value[0].identity_num; //
            oThis.$refs.contProviderRef.comp.contProvider.capital=data.value[0].capital;//
            oThis.$refs.contProviderRef.comp.contProvider.capital_paidin=data.value[0].capital_paidin; //
            oThis.$refs.contProviderRef.comp.contProvider.pk_legal_person=data.value[0].pk_legal_person;//
            oThis.$refs.contProviderRef.comp.contProvider.establish_date=data.value[0].establish_date; //
            oThis.$refs.contProviderRef.comp.contProvider.comm_addr=data.value[0].comm_addr;//
            oThis.$refs.contProviderRef.comp.contProvider.comm_mail=data.value[0].comm_mail;//
            // this.$refs['contProvider_ref'].model.customer_code = data.value[0].refcode;
            // this.$refs['contProvider_ref'].model.society_credit = data.value[0].society_credit;
            // this.$refs['contProvider_ref'].model.capital = data.value[0].capital;
            // this.$refs['contProvider_ref'].model.capital_paidin = data.value[0].capital_paidin;
            // this.$refs['contProvider_ref'].model.legal_person_name = data.value[0].legal_person_name;
            // this.$refs['contProvider_ref'].model.establish_date = data.value[0].establish_date;
            // this.$refs['contProvider_ref'].model.comm_addr = data.value[0].comm_addr;
            // this.$refs['contProvider_ref'].model.comm_mail = data.value[0].comm_mail;
          }
        }
      },
    };
  },
  //
  computed: {
    currentsource_bill() {
      return this.source_bill;
    }
  },
  //
  watch: {
    source_bill(val) {
      this.requestPrjcontProvider();
    }
  },
  //
  created() {
   
  },
  //
  mounted() {
    
    this.request();
  },
  methods: {
    /**
       *   
       **/
    request() {
      if (this.source_bill != '' && this.source_bill != undefined) {
        this.requestPrjcontProvider();
      }
    },
    //
    requestPrjcontProvider() {
      let data = {
        'orderList': [
          {
            'direction': 'desc',
            'property': 'ts'
          }
        ],
        pageNum: 0,
        pageSize: 0,
        searchParams: {
          searchMap: {
            custCondList : [
              {
                'key': 'source_bill',
                'oper': '=',
                'value': this.source_bill
              }
            ]
          }
        }
      };
      this.$http({
        url: ylsBusi + 'prj/provider/page',
        headers: { 'Content-Type': 'application/json' },
        method: 'post',
        data: data,
        dataType: 'json'
      })
        .then(res => {
          let originalValue = res.data.data.content;
          this.$refs['contProviderRef'].setData(
            'contProvider_t',
            JSON.parse(JSON.stringify(originalValue))
          );
        })
        .catch(() => {
          this.$message({
            message: '',
            type: 'error'
          });
        });
    },
    //
    contProviderFormCancel: function(type) {
      this.rmoveindex = '';
      //
      if (type === 'form') {
        this.$refs['contProviderRef'].comp.formShow = false;
      } else {
        this.$refs['contProviderRef'].getTableComp().closeExpandRow();
        var providerBakData = this.$refs.contProviderRef.getData('contProvider_t');
        providerBakData[this.providerEditIndex] = this.providerEditBakData;
        this.$refs.contProviderRef.setData('contProvider_t',providerBakData);
      }
    },
    //
    contProviderFormConfirm: function() {
      //
      let url='';
      let data = this.$refs.contProviderRef.comp.contProvider;
      data.source_bill = this.source_bill;
      if(data.pk_prj_provider){
         url = ylsBusi+'prj/provider/update';
      }else{
         url = ylsBusi+'prj/provider/create';
      }
      //
      this.$refs.contProviderRef.comp.$refs[
        'contProvider_ref'
      ].validate(valid => {
        if (valid) {
          this.$http({
            url: url,
            headers: { 'Content-Type': 'application/json' },
            method: 'post',
            data: JSON.parse(JSON.stringify(data))
          }).then(res => {
            if (res.data.success === true) {
              this.$message({
                message: '',
                type: 'success'
                });
              this.requestPrjcontProvider();
              this.$refs['contProviderRef'].comp.formShow = false;
            } else {
              this.$message({
                message: res.data.error.errorMessage,
                type: 'error'
              });
            }
          }).catch(() => {
            this.$message({
              message: '',
              type: 'error'
            });
          });

          //
          let writeback = {};
          writeback.pk_customer = this.$refs.contProviderRef.comp.contProvider.pk_provider_name; //
          writeback.customer_code = this.$refs.contProviderRef.comp.contProvider.provider_code; //
          writeback.society_credit = this.$refs.contProviderRef.comp.contProvider.identity_num; //
          writeback.capital = this.$refs.contProviderRef.comp.contProvider.capital;//
          writeback.capital_paidin = this.$refs.contProviderRef.comp.contProvider.capital_paidin; //
          writeback.pk_legal_person = this.$refs.contProviderRef.comp.contProvider.pk_legal_person;//
          writeback.establish_date = this.$refs.contProviderRef.comp.contProvider.establish_date; //
          writeback.comm_addr = this.$refs.contProviderRef.comp.contProvider.comm_addr;//
          writeback.comm_mail = this.$refs.contProviderRef.comp.contProvider.comm_mail;//
          
          this.$http({
            url: ylsBusi+'cust/corp/updateOptionalByPkCustomer',
            headers: { 'Content-Type': 'application/json' },
            method: 'post',
            data: JSON.parse(JSON.stringify(writeback))
          }).then(res => {
            if (res.data.success === true) {
              this.$message({
                message: '',
                type: 'success'
              });
            } else {
              this.$message({
                message: res.data.error.errorMessage,
                type: 'error'
              });
            }
          }).catch(() => {
            this.$message({
              message: '',
              type: 'error'
            });
          });
        }
      });
    },
    //
    contProviderFormedit: function(scope) {
      //
      this.rmoveindex = scope.$index;
      //
      let row = scope.row;
      this.$refs['contProviderRef'].getTableComp().expandRow(row);
      this.$refs['contProviderRef'].formShow = false;
      //contProvider
      this.$refs['contProviderRef'].setData('contProvider', row);
      this.providerEditBakData = JSON.parse(JSON.stringify(scope.row));
      this.providerEditIndex = scope.$index;
    },
    // 
    contProviderFormdelete: function(scope) {
      this.contProviderDelVisible = true;
      this.delId = scope.row.pk_prj_provider;
    },
    // 
    contProviderDeleteClick() {
      this.$http({
        url: ylsBusi + 'prj/provider/deleteById',
        headers: { 'Content-Type': 'application/json' },
        method: 'post',
        dataType: 'json',
        data: this.delId
      })
        .then(res => {
          if (res.data.success === true) {
            this.$message({
              message: '',
              type: 'success'
            });
            this.requestPrjcontProvider();
          } else {
            this.$message({
              message: res.data.error.errorMessage,
              type: 'error'
            });
          }
        })
        .catch(e => {
          this.$message({
            message: '',
            type: 'error'
          });
        });
      this.contProviderDelVisible = false;
      this.delId = '';
    }
  }
};
</script>
<style>

</style>



